import { Row, Col } from 'antd';
import { Suspense, useCallback, useEffect, useState } from 'react';
import {
    BrowserRouter as Router,
    Route,
    Routes,
} from "react-router-dom";
import './layout.css';
// import 'react-pro-sidebar/dist/css/styles.css';
import LoadMenu from './loadMenu';
import MyRouter from './myRouter';
import { MyAxiosV2 } from '../../helper/myaxios';
import { AuthService } from '../../services/authService';
import { LoginResponseModel } from '../../models/authModels';
import routes from '../../router/router';
import { MenuOutlined } from "@ant-design/icons";
import { MyButton } from '../core/MyButton';
import LanguageComponent from './language';


export default function Layout(props: any) {
    function doRefreshToken(isRemoveToken: boolean) {
        if ((MyAxiosV2.AccessToken !== null && MyAxiosV2.RefreshToken !== null)) {
            AuthService.RefreshToken()
                .then((data) => {
                    if (data.isSuccess) {
                        new LoginResponseModel(data.data);
                    }
                    else {
                        if (isRemoveToken) {
                            MyAxiosV2.SetAccessToken(null);
                            MyAxiosV2.SetRefreshToken(null);
                        }
                    }
                })
        }
    }
    const time = 70000;

    const refreshToken = useCallback(() => {
        setInterval(() => {
            doRefreshToken(false);
        }, time);
    }, [time]);

    useEffect(() => {
        refreshToken();
    }, [refreshToken]);

    function RenderComponent(routes: any[]) {
        var data;
        data = routes.map((data: any, index: number) => {
            return (
                <Route
                    key={index}
                    path={data.path}
                    element={data.main}
                />);
        });
        return data;
    }

    const [visible, setVisible] = useState(false);
    function handleClickMenu() {
        setVisible(true);
    }

    return (
        <div className="">
            <LanguageComponent />
            <Router>
                <Row gutter={12}>
                    <Col span={2} xl={2} md={1} xs={1}>
                        <LoadMenu
                            visible={visible}
                            setVisible={setVisible} />
                    </Col>
                    <Col span={22} xl={22} md={23} xs={23}>
                        <MyButton onClick={handleClickMenu} value={<MenuOutlined />} />
                        <MyRouter />
                        <Suspense fallback={<div>Page is Loading...</div>}>
                            <Routes>
                                {RenderComponent(routes)}
                            </Routes>
                        </Suspense>
                    </Col>
                </Row>
            </Router>
        </div>
    );
}