import { Button } from 'antd';
// import Loading from './Loading';
import './UIStyle.css';

export function MyButton(props: any) {
    let htmlType = props.htmlType !== undefined ? props.htmlType : "button";
    let sharp = props.sharp !== undefined ? props.sharp : "round";

    function onClick() {
        if (props.onClick !== undefined) {
            props.onClick();
        }
    }

    return (
        <Button
            shape={sharp}
            style={props.style}
            disabled={props.disabled}
            loading={props.isLoading}
            htmlType={htmlType}
            className={`btn_${props.type} ${props.className}`}
            onClick={onClick}
            onFocus={props.onFocus}
            onDoubleClick={props.onDoubleClick}>
            {/* <Loading isLoading={props.isLoading} />  */}
            {props.value}
        </Button>
    );
}

export function MyButtonWrap(props: any) {
    let htmlType = props.htmlType !== undefined ? props.htmlType : "button";
    let sharp = props.sharp !== undefined ? props.sharp : "round";

    function onClick() {
        if (props.onClick !== undefined) {
            props.onClick();
        }
    }

    return (
        <Button
            shape={sharp}
            style={props.style}
            disabled={props.disabled}
            loading={props.isLoading}
            htmlType={htmlType}
            className={`btn_${props.type} ${props.className}`}
            onClick={onClick}
            onFocus={props.onFocus}
            onDoubleClick={props.onDoubleClick}>
            {/* <Loading isLoading={props.isLoading} />  */}
            <span style={{ "wordBreak": "break-all" }}>{props.value}</span>
        </Button>
    );
}