import { Button } from "antd";
import { LeftOutlined } from "@ant-design/icons";
import { useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { MyAxiosV2 } from "../../helper/myaxios";
function MyRouter() {
  let location = useLocation();
  let navigate = useNavigate();

  useEffect(() => {
    if (MyAxiosV2.AccessToken !== null && (location.pathname === "/login" || location.pathname === "/forgotPassword")) {
      navigate("/");
    }

    if (MyAxiosV2.AccessToken === null && (location.pathname !== "/login" && location.pathname !== "/forgotPassword")) {
      navigate("/login");
      MyAxiosV2.SetAccessToken(null);
    }    
  }, [location, navigate]);

  return (
    <div>
      <Button onClick={() => { navigate(-1) }}>
        <LeftOutlined />
        Tr v
      </Button>
    </div>
  );
}

export default MyRouter
