import { lazy } from "react";
import { Test } from "../../components/tool/test/testcomponent";

const ToolTaskIndexComponent = lazy(() => import("../../components/tool/task/TaskIndexComponent"));
const ToolTaskProjectManageIndexComponent = lazy(() => import("../../components/tool/task/projectManage/IndexComponent"));
const ToolTaskProjectManageModifyComponent = lazy(() => import("../../components/tool/task/projectManage/ModifyComponent"));
const ToolTaskSprintManageIndexComponent = lazy(() => import("../../components/tool/task/sprintManage/IndexComponent"));
const ToolTaskSprintManageDetailComponent = lazy(() => import("../../components/tool/task/sprintManage/DetailComponent"));
const ToolTaskSettingIndexComponent = lazy(() => import("../../components/tool/task/settings/SettingComponent"));
const ToolTaskProjectCloneComponent = lazy(() => import("../../components/tool/task/projectManage/CloneComponent"));
const ToolTaskProjectWorkflowIndexComponent = lazy(() => import("../../components/tool/task/workflow/IndexComponent"));
const ToolTaskProjectWorkflowVariableIndexComponent = lazy(() => import("../../components/tool/task/workflow/variable/IndexComponent"));
const ToolTaskProjectDocumentIndexComponent = lazy(() => import("../../components/tool/task/workflow/document/IndexComponent"));
const ToolTaskProjectDocumentModifyComponent = lazy(() => import("../../components/tool/task/workflow/document/ModifyApiDocComponent"));
const ToolTaskProjectSprintToDoListIndexComponent = lazy(() => import("../../components/tool/task/toDoList/IndexComponent"));
const ToolTaskWorkflowIndexComponent = lazy(() => import("../../components/tool/task/workflow/index/IndexComponent"));
const ToolTaskWorkflowDetailIndexComponent = lazy(() => import("../../components/tool/task/workflow/index/DetailIndexComponent"));
const ToolTaskWorkflowRunIndexComponent = lazy(() => import("../../components/tool/task/workflow/run/IndexComponent"));
const ToolTaskWorkflowRunDetailComponent = lazy(() => import("../../components/tool/task/workflow/run/DetailComponent"));

export const ToolModule = [
    {
        path: '/tools/tasks',
        main: < ToolTaskIndexComponent />
    },
    {
        path: '/tools/tasks/projects',
        main: < ToolTaskProjectManageIndexComponent />
    },
    {
        path: '/tools/tasks/projects/add',
        main: < ToolTaskProjectManageModifyComponent />
    },
    {
        path: '/tools/tasks/projects/:key',
        main: < ToolTaskProjectManageModifyComponent />
    },
    {
        path: '/tools/tasks/projects/:key/sprint',
        main: < ToolTaskSprintManageIndexComponent />
    },
    {
        path: '/tools/tasks/projects/:key/sprint/:sprintKey/toDoList',
        main: < ToolTaskProjectSprintToDoListIndexComponent />
    },
    // {
    //     path: '/tools/tasks/projects/:key/sprint/edit/:key1',
    //     main: < ToolTaskSprintManageDetailComponent />
    // },
    {
        path: '/tools/tasks/projects/:key/sprint/add',
        main: < ToolTaskSprintManageDetailComponent />
    },
    {
        path: '/tools/tasks/projects/:key/settings',
        main: <ToolTaskSettingIndexComponent />,
    },
    {
        path: '/tools/tasks/projects/:key/clone',
        main: <ToolTaskProjectCloneComponent />,
    },
    {
        path: '/tools/tasks/projects/:key/codeManage',
        main: <ToolTaskProjectWorkflowIndexComponent />,
    },
    {
        path: '/tools/tasks/projects/:key/codeManage/variable',
        main: <ToolTaskProjectWorkflowVariableIndexComponent />,
    },
    {
        path: '/tools/tasks/projects/:key/codeManage/document',
        main: <ToolTaskProjectDocumentIndexComponent />,
    },
    {
        path: '/tools/tasks/projects/:projectKey/codeManage/document/:folderKey/apis/add',
        main: <ToolTaskProjectDocumentModifyComponent />,
    },
    {
        path: '/tools/tasks/projects/:projectKey/codeManage/document/:folderKey/apis/:key',
        main: <ToolTaskProjectDocumentModifyComponent />,
    },
    {
        path: '/tools/tasks/workflows',
        main: < ToolTaskWorkflowIndexComponent />
    },
    {
        path: '/tools/tasks/projects/:projectKey/workflows/:workflowKey/details',
        main: < ToolTaskWorkflowDetailIndexComponent />
    },
    {
        path: '/tools/tasks/projects/:projectKey/workflows/:workflowKey/run',
        main: < ToolTaskWorkflowRunIndexComponent />
    },
    {
        path: '/tools/tasks/projects/:projectKey/workflows/:workflowKey/run/:key/detail',
        main: < ToolTaskWorkflowRunDetailComponent />
    },
    {
        path: '/tools/tasks/projects/:projectKey/workflows/:workflowKey/run/add',
        main: < ToolTaskWorkflowRunDetailComponent />
    },
    {
        path: '/test',
        main: <Test />,
    },
];