import { MyAxiosV2 } from "../helper/myaxios";

export class HomeService {
    static GetListLogVersion() {
        var url = `v2/home/aboutVersion`;
        return MyAxiosV2.SendReq(url, "Get");
    }

    static GetListMenu(isMobi: boolean) {
        var url = `v2/home/renderMenus?isMobi=${isMobi}`;
        return MyAxiosV2.SendReq(url, "Get");
    }
}