import { message } from "antd";

export class MyAxiosV2 {
    static AccessToken: string | null = localStorage.getItem('accessToken');
    static Lang: string | null = localStorage.getItem('lang');
    static BaseURL: string | undefined = "https://myweb-api.huynq.xyz/api";
    // static BaseURL: string | undefined = "https://myweb-api-dev.huynq.xyz/api";

    static SetAccessToken(token: string | null) {
        this.AccessToken = token;
        this.UpdateTokenMyAxios();

        if (token === null) {
            localStorage.removeItem("accessToken");
        } else {
            localStorage.setItem("accessToken", token);
        }
    }

    static GetAccessToken() {
        return this.AccessToken;
    }

    // refreshToken
    static RefreshToken: string | null = localStorage.getItem('refreshToken');
    static SetRefreshToken(token: string | null) {
        this.RefreshToken = token;
        if (token === null) {
            localStorage.removeItem("refreshToken");
        } else {
            localStorage.setItem("refreshToken", token);
        }
    };

    static headers = {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': this.AccessToken !== null ? `Bearer ${this.AccessToken}` : '',
            'Accept-Language': this.Lang,
            'Language': this.Lang,
            // 'Content-Type': 'application/x-www-form-urlencoded',
        },
        method: "GET",
        body: JSON.stringify({})
    }

    static UpdateTokenMyAxios() {
        this.headers = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': this.AccessToken !== null ? `Bearer ${this.AccessToken}` : '',
                'Accept-Language': this.Lang,
                'Language': this.Lang,
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            method: "GET",
            body: JSON.stringify({})
        }
    }

    static async SendReq(url: string, method: string, data?: any, isBlob?: boolean) {
        this.headers.method = method;

        let _headers: any = this.headers.headers;
        if (isBlob && method === "Post") {
            this.headers.body = data;
            _headers = {
                'Authorization': this.AccessToken !== null ? `Bearer ${this.AccessToken}` : '',
                'Accept-Language': this.Lang,
                'Language': this.Lang,
            };
        } else {
            this.headers.body = JSON.stringify(data);
        }
        var response:Response = {
            isSuccess: true,
            message: "",
            data: {}
        };

        try {
            await fetch(`${this.BaseURL}/${url}`, {
                headers: _headers,
                method: method,
                body: this.headers.body
            })
                .then((res) => {
                    if (isBlob && method !== "Post") {
                        return res.blob();
                    }
                    if (!res.ok && res.status !== 400) {
                        response = {
                            isSuccess: false,
                            message: "Server error: " + res.status,
                            data: {}
                        };
                        message.error(response.message)
                    }
                    return res.json()
                })
                .then((data: any) => {
                    if (isBlob && method !== "Post") {
                        response = {
                            isSuccess: true,
                            message: "",
                            data: data
                        };
                        return;
                    }

                    if (data.statusCode >= 500) { return; } //  catch  trn
                    if (data.statusCode !== 200) {
                        response = {
                            isSuccess: false,
                            message: data.message,
                            data: data.data
                        };
                        message.error(data.message)
                    } else {
                        response = {
                            isSuccess: true,
                            message: data.message,
                            data: data.data
                        };
                    }
                })
                .catch((err) => { response = err });
        }
        catch (err) {
            console.log(err);
        }
        return response;
    }
}

class Response {
    isSuccess: boolean | undefined;
    message: string | undefined;
    data: any
}