import { MyAxiosV2 } from "../helper/myaxios";
import { LoginModel } from "../models/authModels";

export class AuthService {
    static CheckLogin(loginModel: LoginModel) {
        let url = "v2/users/userSession";
        return MyAxiosV2.SendReq(url, "Post", loginModel);
    }

    static RefreshToken = () => {
        var url = "v2/users/userSession";
        var data = {
            refreshToken: MyAxiosV2.RefreshToken
        }
        return MyAxiosV2.SendReq(url, "Put", data);
    }

    static GetProfile = () => {
        var url = "v2/users/me";
        return MyAxiosV2.SendReq(url, "Get");
    }

    static UpdateProfile = (body: any) => {
        var url = "v2/users/info";
        return MyAxiosV2.SendReq(url, "Put", body);
    }

    static UpdatePassword = (body: any) => {
        var url = "v2/users/password";
        return MyAxiosV2.SendReq(url, "Put", body);
    }

    static RequestForgotPassword = (body: any) => {
        var url = "v2/users/forgotPassword";
        return MyAxiosV2.SendReq(url, "Post", body);
    }

    static VerifyForgotPassword = (body: any) => {
        var url = "v2/users/confirmForgotPassword";
        return MyAxiosV2.SendReq(url, "Post", body);
    }
}

