import { useEffect, useState } from "react";
import { Box } from "../core/MyBox";

export default function LanguageComponent(props: any) {
    const [lang, setLang] = useState(localStorage.getItem('lang')?.toString());

    function handleChangeLang(val: any) {
        setLang(val.target.value);
        localStorage.setItem('lang', val.target.value);
        window.location.reload();
    }

    useEffect(() => {
        if(lang === undefined) {
            setLang('vi');
            localStorage.setItem('lang', 'vi');
        }        
    }, [lang]);
    
    return (
        <Box>
            <select value={lang} onChange={handleChangeLang}>
                <option value="vi">Ting Vit</option>
                <option value="en">Ting Anh</option>
            </select>
        </Box>
    )
}