import { lazy } from "react";

const LoginComponent = lazy(() => import("../../components/auth/Login"));
const ForgotPasswordComponent = lazy(() => import("../../components/auth/ForgotPasswordComponent"));
const AboutComponent = lazy(() => import("../../components/home/About"));
const HomeComponent = lazy(() => import("../../components/home/HomeComponent"));
const ChangeInfoComponent = lazy(() => import("../../components/profile/ChangeInfoComponent"));
const ChangePasswordComponent = lazy(() => import("../../components/profile/ChangePasswordComponent"));
const LogoutComponent = lazy(() => import("../../components/profile/logout"));

export const UserModule = [
    {
        path: '/login',
        main: < LoginComponent />
    },
    {
        path: '/forgotPassword',
        main: <ForgotPasswordComponent />
    },
    {
        path: '/',
        main: < HomeComponent />
    },
    {
        path: '/about',
        main: < AboutComponent />
    },
    {
        path: '/me/logout',
        main: < LogoutComponent />
    },
    {
        path: 'me/info',
        main: <ChangeInfoComponent />
    },
    {
        path: 'me/password',
        main: <ChangePasswordComponent />
    }
];