import { lazy } from "react";

const ManageLinkStoredIndexComponent = lazy(() => import("../../components/manage/storedManage/Link_IndexComponent"));
const ManageStoredManageIndexComponent = lazy(() => import("../../components/manage/storedManage/IndexComponent"));
const ManageMailAppIndexComponent = lazy(() => import("../../components/manage/email/MailApp_IndexComponent"));
const ManageMenuIndexComponent = lazy(() => import("../../components/manage/menu/IndexComponent"));
const ManageTemplateMailIndexComponent = lazy(() => import("../../components/manage/email/TemplateMail_IndexComponent"));
const ManageTemplateMailModifyComponent = lazy(() => import("../../components/manage/email/TemplateMail_ModifyComponent"));
const ManageStoredManageDetailComponent = lazy(() => import("../../components/manage/storedManage/DetailComponent"));
const ManageLogCliIndexComponent = lazy(() => import("../../components/manage/logCli/IndexComponent"));
const ManageLogCliChartComponent = lazy(() => import("../../components/manage/logCli/ChartComponent"));
const LogActionComponent = lazy(() => import("../../components/manage/logCli/LogActionComponent"));
const ManageVersionIndexComponent = lazy(() => import("../../components/manage/versionManage/IndexComponent"));

export const AdminModule = [
    {
        path: '/admin/storedManage',
        main: < ManageStoredManageIndexComponent />
    },
    {
        path: '/admin/storedManage/add',
        main: < ManageStoredManageDetailComponent />
    },
    {
        path: '/admin/appMail',
        main: < ManageMailAppIndexComponent />
    },
    {
        path: '/admin/template',
        main: <ManageTemplateMailIndexComponent />
    },
    {
        path: '/admin/template/add',
        main: <ManageTemplateMailModifyComponent />
    },
    {
        path: '/admin/template/edit/:key',
        main: <ManageTemplateMailModifyComponent />
    },
    {
        path: '/admin/logCli',
        main: <ManageLogCliIndexComponent />
    },
    {
        path: '/admin/logAction',
        main: <LogActionComponent />
    },
    {
        path: '/admin/chart',
        main: <ManageLogCliChartComponent />
    },
    {
        path: '/admin/storedManage/linkStored',
        main: < ManageLinkStoredIndexComponent />
    },
    {
        path: '/admin/storedManage/edit/:key',
        main: < ManageStoredManageDetailComponent />
    },
    {
        path: '/admin/versionManage',
        main: < ManageVersionIndexComponent />
    },
    {
        path: 'admin/menu',
        main: < ManageMenuIndexComponent />
    }
];